.samples-stream-tags {

    display: flex;
    flex-wrap: wrap;

    .samples-stream-tag {
        border: 1px solid transparent;
    }

    .samples-stream-tag:hover {
        border: 1px solid white;
    }

    .samples-stream-tag.expanded {
        background-color: white;
        color: black;
        width: 100%;

        .sample-stream-option {
            .s-name {
                color: black !important;
            }
        }
    }
}
