.spore-editor {
    .chain-loader {
        margin-top: 15px;
        border-top: 1px solid white;
        width: 100%;
        background-color: #000000ba;

        .chain-load-option:hover {
            background-color: white;
            color: black;
            .hyphen {
                background-color: black;
            }
        }
        .chain-load-option {
            display: flex;
            padding: 5px;
            cursor: pointer;

            .chain-id {
                margin-right: 5px;
            }

            .hyphen {
                width: 15px;
                height: 3px;
                margin-top: 12px;
                margin-left: 8px;
                margin-right: 8px;
                background-color: white;
            }
        }
    }
}
